angular
  .module('app', [
    'ui.router',
    'ui.bootstrap',
    'ngMessages',
    'angular-loading-bar',
    'LocalStorageModule',
    'toastr',
    'ngAnimate',
    'daterangepicker'
  ])
  .config(function ($httpProvider, $compileProvider) {
    $httpProvider.interceptors.push('AuthInterceptor');
    $httpProvider.useApplyAsync(true);
    $compileProvider.debugInfoEnabled(false);
  })
  .run(function ($transitions, $q) {
    var match = {
      to: function (state) {
        return state.data && state.data.roles;
      }
    };
    $transitions.onStart(match, function (trans) {
      var Auth = trans.injector().get('Auth');
      var state = trans.$to();
      return Auth.getCurrentUser()
        .then(function (user) {
          if(!user.hasOwnProperty('role') || state.data.roles.indexOf(user.role) === -1){
            return $q.reject('access error');
          }
        });
    });
    $transitions.onError(match, function (trans) {
      var $state = trans.router.stateService;
      $state.go('login');
    });
  });


(function () {
  angular
    .module('app')
    .component('serialDetailComponent', {
      templateUrl: 'app/components/serial-numbers-list/serial-detail/serial-detail.component.html',
      controller: serialDetailComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function serialDetailComponent(Serial, toastr, $scope, $stateParams, $uibModal, Activation, localStorageService ) {
    var vm = this;
    vm.$onInit = function () {
      vm.activations = [];
      vm.loadSerial();
      vm.sortFilters = localStorageService.get('activationsSortFilters') || {
        sort: 'dateCreate',
        order: true
      };
      vm.pagination = {
        page: 1,
        limit: 10
      };
      vm.filters = localStorageService.get('activationFilters') || {};
      $scope.$watch('vm.filters', function () {
        localStorageService.set('activationFilters', vm.filters);
        vm.loadSerial();
      }, true);
    };
    vm.loadSerial = function () {
      Serial.getOne($stateParams.id)
        .then(function (response) {
          vm.serial = response.data;
          vm.loadActivations();
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });
    };
    vm.activate = function(){
      $uibModal.open({
        component: 'createActivationComponent',
        resolve: {
          serial: function(){
            return vm.serial.key;
          }
        }
      }).result.then(function(){
        vm.loadSerial();
      })
    };
    vm.loadActivations = function(){
      var query = '?serial=' + vm.serial.key + '&';
      for(var key in vm.filters) {
        if(vm.filters[key]){
          query = query + key + '=' + vm.filters[key] + '&'
        }
      }
      query = query + 'sort=' + (vm.sortFilters.order ? '' : '-') + vm.sortFilters.sort + '&';
      query = query + 'page=' + vm.pagination.page + '&';
      query = query + 'limit=' + vm.pagination.limit;
      Activation.getAll(query)
        .then(function(response){
          vm.activations = response.data.docs;
          vm.pagination.total = response.data.total;
          vm.pagination.page = response.data.page;
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });
    }
  }

})();

(function () {
  angular
    .module('app')
    .component('createSerialGroupComponent', {
      templateUrl: 'app/components/serial-numbers-group/create/SNG-create.component.html',
      bindings: {
        resolve: '<',
        close: '&',
        dismiss: '&'
      },
      controller: createSerialGroupComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function createSerialGroupComponent(SerialGroup, Product, toastr) {
    var vm = this;
    vm.$onInit = function () {
      vm.serialGroup = vm.resolve.serialGroup;
      vm.loadProducts();
    };
    vm.cancel = function () {
      vm.dismiss({$value: 'cancel'});
    };
    vm.save = function () {
      if(vm.serialGroup._id){
        SerialGroup.update(vm.serialGroup)
          .then(function () {
            vm.close({$value: vm.serialGroup});
          })
          .catch(function (error) {
            var message;
            if (error.data && error.data.message) {
              message = error.data.message;
            } else {
              message = 'Something went wrong';
            }
            toastr.error(message, 'Error');
          });
      } else {
        SerialGroup.create(vm.serialGroup)
          .then(function (response) {
            vm.close({$value: response.data});
          })
          .catch(function (error) {
            var message;
            if (error.data && error.data.message) {
              message = error.data.message;
            } else {
              message = 'Something went wrong';
            }
            toastr.error(message, 'Error');
          });
      }
    };

    vm.loadProducts = function () {
      Product.getAll('')
        .then(function (response) {
          vm.products = response.data.docs;
        })
    };
  }
})();

