(function () {
  angular
    .module('app')
    .component('createActivationComponent', {
      templateUrl: 'app/components/activation/create/create-activation.html',
      controller: createActivationComponent,
      controllerAs: 'vm',
      bindings: {
        resolve: '<',
        close: '&',
        dismiss: '&'
      }
    });

  /** @ngInject */
  function createActivationComponent(Activation, toastr, $uibModal) {
    var vm = this;
    vm.$onInit = function(){
        vm.data = {};
        vm.data.serial = vm.resolve.serial;
    };

    vm.save = function(){
        Activation.create(vm.data)
        .then(function(response){
            // vm.close(response.data);
            if(response.data.status){
                $uibModal.open({
                    component: 'alertComponent',
                    backdrop: false,
                    resolve: {
                        title: function(){
                            return 'Activation Completed'
                        },
                        message: function(){
                            return 'Activation Key: ' + response.data.activationKey
                        }
                    }
                })
                .result.then(function(){
                    vm.close()
                })
            } else{
                toastr.error(response.data.reason, 'Error')
            }
        })
        .catch(function(err){
            toastr.error('Something went wrong', 'Error')
        })
    };
    vm.cancel = function () {
      vm.dismiss({$value: 'cancel'});
    };
  }
})();

(function () {
  angular
    .module('app')
    .component('serialNumbersListComponent', {
      templateUrl: 'app/components/serial-numbers-list/serial-numbers-list.component.html',
      controller: serialNumbersListComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function serialNumbersListComponent(Serial ,SerialGroup, $scope, localStorageService, $uibModal, toastr, $stateParams) {
    var vm = this;
    vm.$onInit = function () {
      vm.serials = [];
      vm.getThisGroup();
      vm.thisGroup = {};
      vm.sortFilters = localStorageService.get('serialsSortFilters') || {
          sort: 'docIndex',
          order: true
        };
      vm.pagination = {
        page: 1,
        limit: 10
      };
      vm.filters = localStorageService.get('serialsFilters') || {};
      $scope.$watch('vm.filters', function () {
        vm.loadSerials();
      }, true);
    };

    vm.getThisGroup = function() {
      SerialGroup.getOne($stateParams.id)
        .then(function (response) {
          vm.thisGroup = response.data;
        })
    };

    vm.search  = function () {
      localStorageService.set('serialsFilters', vm.filters);

      vm.loadSerials();
    };
    vm.reset = function () {
      vm.filters = {};
    };

    vm.onSortFiltersChanged = function (key) {
      if(vm.sortFilters.sort === key){
        vm.sortFilters.order = !vm.sortFilters.order;
      } else {
        vm.sortFilters.sort = key;
        vm.sortFilters.order = true;
      }
      localStorageService.set('serialsSortFilters', vm.sortFilters);
      vm.loadSerials();
    };

    vm.loadSerials = function () {
      var query = '?' + 'serialGroup=' + $stateParams.id + '&';
      for(var key in vm.filters) {
        if(vm.filters[key]){
          query = query + key + '=' + vm.filters[key] + '&'
        }
      }
      query = query + 'sort=' + (vm.sortFilters.order ? '' : '-') + vm.sortFilters.sort + '&';
      query = query + 'page=' + vm.pagination.page + '&';
      query = query + 'limit=' + vm.pagination.limit;
      Serial.getAll(query)
        .then(function (response) {
          vm.serials = response.data.docs;
          vm.pagination.total = response.data.total;
          vm.pagination.page = response.data.page;
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });
    };
    vm.delete = function (serial) {
      $uibModal.open({
        animation: true,
        component: 'confirmComponent',
        size: 'sm',
        resolve: {
          message: function () {
            return 'Are you sure to delete serial?';
          }
        }
      }).result.then(function () {
        Serial.delete(serial)
          .then(function () {
            vm.loadSerials();
          })
          .catch(function () {
            toastr.error('Something went wrong', 'Error');
          })
      });
    };
  }

})();

(function () {
  angular
    .module('app')
    .component('serialNumbersGroupComponent', {
      templateUrl: 'app/components/serial-numbers-group/serial-numbers-group.component.html',
      controller: serialNumberGroupComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function serialNumberGroupComponent(SerialGroup, Product, localStorageService, $uibModal, toastr, $state, $stateParams, $scope) {
    var vm = this;
    vm.$onInit = function () {
      vm.serialGroups = [];
      vm.products = [];
      vm.sortFilters = localStorageService.get('serialGroupSortFilters') || {
          sort: 'docIndex',
          order: true
        };
      vm.pagination = {
        page: 1,
        limit: 10
      };
      if ($stateParams.productId || $stateParams.serialPrefix) {
        vm.filters = {
          productId: $stateParams.productId,
          serialPrefix: $stateParams.serialPrefix
        };
        localStorageService.set('serialGroupFilters', vm.filters);
      } else {
        vm.filters = localStorageService.get('serialGroupFilters') || {};
        $state.transitionTo('serialGroups', vm.filters);
      }
      vm.filters = localStorageService.get('serialGroupFilters') || {};
      $scope.$watch('vm.filters', vm.onFiltersChanged, true);
      vm.loadSerialGroups();
      vm.loadProducts();
    };

    vm.loadProducts = function () {
      Product.getAll('')
        .then(function (response) {
          vm.products = response.data.docs;
          console.log(vm.products)
        })
    };

    vm.uiOnParamsChanged = function (newParams) {
      for (var key in newParams) {
        if (vm.filters[key] !== newParams[key]) {
          vm.filters[key] = newParams[key];
        }
      }
    };

    vm.onFiltersChanged  = function () {
      $state.transitionTo('serialGroups', vm.filters);
      localStorageService.set('serialGroupFilters', vm.filters);
      vm.loadSerialGroups();
    };

    vm.resetFilters = function () {
      vm.filters = {};
    };

    vm.onSortFiltersChanged = function (key) {
      if(vm.sortFilters.sort === key){
        vm.sortFilters.order = !vm.sortFilters.order;
      } else {
        vm.sortFilters.sort = key;
        vm.sortFilters.order = true;
      }
      localStorageService.set('serialGroupSortFilters', vm.sortFilters);
      vm.loadSerialGroups();
    };

    vm.loadSerialGroups = function () {
      var query = '?';
      for(var key in vm.filters) {
        if(vm.filters[key]){
          query = query + key + '=' + vm.filters[key] + '&'
        }
      }
      query = query + 'sort=' + (vm.sortFilters.order ? '' : '-') + vm.sortFilters.sort + '&';
      query = query + 'page=' + vm.pagination.page + '&';
      query = query + 'limit=' + vm.pagination.limit;
      SerialGroup.getAll(query)
        .then(function (response) {
          vm.serialGroups = response.data.docs;
          vm.pagination.total = response.data.total;
          vm.pagination.page = response.data.page;
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });
    };

    vm.openAddSerialModal = function (serialGroup, index) {
      var modalInstance = $uibModal.open({
        animation: true,
        component: 'createSerialGroupComponent',
        resolve: {
          serialGroup: function () {
            return angular.copy(serialGroup);
          }
        }
      });

      modalInstance.result.then(function () {
        vm.loadSerialGroups();
      });
    };
    vm.delete = function (serialGroup) {
      $uibModal.open({
        animation: true,
        component: 'confirmComponent',
        size: 'sm',
        resolve: {
          message: function () {
            return 'Are you sure to delete serials group?';
          }
        }
      }).result.then(function () {
        SerialGroup.delete(serialGroup)
          .then(function () {
            vm.loadSerialGroups();
          })
          .catch(function () {
            toastr.error('Something went wrong', 'Error');
          })
      });
    };
  }
})();

